var q;
var s;
var price;
var f;
var u;
function letsReview(variantID, qty, subID, frequency, unitType, subLength) {
  var html = '';
  v = activeVID;
  q = activeQty;
  s = subID;
  f = frequency;
  u = unitType;
  formFinished = true;
  $('.lets_review').removeClass('hide');
  $('.button-container').removeClass('hide');

  if (isGift) {
    console.log(activeQty);
    if (activeQty == 2) {
      price = (0, _themeCurrency.formatMoney)(activeV.price * 2, theme.moneyFormat);
    } else {
      price = (0, _themeCurrency.formatMoney)(activeV.price, theme.moneyFormat);
    }
  } else {
    for (var i = 0; i < x.variants.length; i++) {
      if (x.variants[i].id == variantID) {

        if (activeQty == 2) {
          price = (0, _themeCurrency.formatMoney)(x.variants[i].price * 2, theme.moneyFormat);
        } else {
          price = (0, _themeCurrency.formatMoney)(x.variants[i].price, theme.moneyFormat);
        }
      }
    }
  }

  if (isGift) {
    if (giftType == 'coffee') {
      html += '<li class="qty-txt">COFFEE</li>';
    } else {
      html += '<li class="qty-txt">TEA</li>';
    }
  }

  selection.forEach(function (item, index) {
    if (item != 'Default Title') {
      html += '<li class="option-' + index + '">' + item + '</li>';
    }
  });
  if (subscriptionTxt != '') {
    html += '<li class="sub-txt">' + subscriptionTxt + '</li>';
  }

  if (isGift) {
    if (giftType == 'coffee') {
      html += '<li class="qty-txt">' + qty + ' BAGS</li>';
    } else {
      html += '<li class="qty-txt">' + qty + ' BOXES</li>';
    }

    html += '<li class="qty-txt">' + subLength + ' MONTHS</li>';
  } else {
    html += '<li class="qty-txt">' + qty + ' BAG</li>';
  }

  $('.add-sub-to-cart .price').html(' - ' + price);
  $('.lets_review--readout').html('<ul>' + html + '</ul>');
}

function reset() {

  selection = [];
  subID;
  subscription = false;

  $('.product_content--quick-add-btn').removeClass('selected');

  if (isSubPage) {
    $('.lets_review--readout').html('');
  }
}

$(document).on('click', '.add-sub-to-cart', function (e) {
  e.preventDefault();
  var productID = $(this).attr('data-product-id');
  addItemToCart(v, q, s, productID, f, u);
});

function addItemToCart(variant_id, qty, selling_plan, productID, frequency, unitType) {

  // return;


  if (subscription) {

    var data;
    variant_id = subscriptions.original_to_hidden_variant_map[variant_id];
    variant_id = variant_id.discount_variant_id;

    data = {
      "id": variant_id,
      "quantity": activeQty,
      "properties": {
        "shipping_interval_frequency": frequency,
        "shipping_interval_unit_type": unitType
      }
    };

    jQuery.ajax({
      type: 'POST',
      url: '/cart/add.js',
      data: data,
      dataType: 'json',
      success: function success() {
        $('.success-' + productID).removeClass('hide');
        $('.button-container-' + productID).addClass('hide');

        $.get('/cart.js', '', function (data) {
          $('.cart-count').html(data.item_count);
          CartJS.getCart();
          showDropdown(productID);
          setTimeout(function () {
            $('.success-' + productID).addClass('hide');
            $('.variant-select-container-start-' + productID).removeClass('hide');
            $('.button-container-' + productID).removeClass('hide');

            // $('.variant-select-container').removeClass('hide');
          }, 3000);
        }, 'json');
      },
      error: function error(req, status, _error) {
        $('.error-' + productID).removeClass('hide');
        $('.button-container-' + productID).addClass('hide');
        setTimeout(function () {
          $('.error-' + productID).addClass('hide');
          $('.button-container-' + productID).removeClass('hide');
        }, 3000);
        // $('.variant-select-container').addClass('hide');
      }
    });
  } else {
    CartJS.addItem(activeVID, activeQty, {}, {
      success: function success(data, textStatus, jqXHR) {
        $('.success-' + productID).removeClass('hide');
        $('.button-container-' + productID).addClass('hide');

        $.get('/cart.js', '', function (data) {
          $('.cart-count').html(data.item_count);
          showDropdown(productID);
          setTimeout(function () {
            $('.success-' + productID).addClass('hide');
            $('.variant-select-container-start-' + productID).removeClass('hide');
            $('.button-container-' + productID).removeClass('hide');

            // $('.variant-select-container').removeClass('hide');
          }, 3000);
        }, 'json');
      },
      error: function error(req, status, _error2) {
        $('.error-' + productID).removeClass('hide');
        $('.button-container-' + productID).addClass('hide');
        setTimeout(function () {
          $('.error-' + productID).addClass('hide');
          $('.button-container-' + productID).removeClass('hide');
        }, 3000);
        // $('.variant-select-container').addClass('hide');

      }
    });
  }
}

function reload() {
  var container = document.getElementById("cart-drawer");
  var content = container.innerHTML;
  container.innerHTML = content;

  //this line is to watch the result in console , you can remove it later
}

function showDropdown(productID) {

  $("#cart-drawer").css('visibility', 'visible');
  $("#cart-drawer").animate({
    opacity: 1
  }, 200, function () {
    // Animation complete.
    setTimeout(function () {
      $("#cart-drawer").animate({
        opacity: 0
      }, 200, function () {
        // Animation complete.
        $("#cart-drawer").css('visibility', 'hidden');
      });
    }, 2000);
  });
}

